import TitleHeader from "@Components/TitleHeader/TitleHeader";
import {
  Button,
  Row,
  Col,
  Divider,
  Typography,
  Radio,
  Space,
  Tag,
  Badge,
  Card,
} from "antd";
import { useCart } from "@Utils/Cart/Cart";
import MockData from "@Mocks/MockData";
import { ProDescriptions } from "@ant-design/pro-components";
import { useNavigate } from "react-router-dom";
import { useEffect, useState } from "React";

const shippingMethods = [
  {
    id: "7-11",
    price: 50,
    name: "7-11 取貨",
    free: 888,
  },
  {
    id: "familyMart",
    price: 50,
    name: "全家 取貨",
    free: 888,
  },
];

const getShippingFee = function (totalPrice, shippingMethod) {
  if (totalPrice >= shippingMethod.free) {
    return 0;
  } else {
    return shippingMethod.price;
  }
};

const App = () => {
  const navigate = useNavigate();
  const cart = useCart();
  const [shippingFee, setShippingFee] = useState(null);
  const totalProduct = cart.current
    .map((x) => x.quality)
    .reduce((x, y) => x + y, 0);
  const totalAmount = cart.current
    .map((x) => {
      const data = MockData.all.find((x) => x.id === x.id);
      return data.price * x.quality;
    })
    .reduce((x, y) => x + y, 0);

  useEffect(() => {
    if (cart.current.length <= 0) {
      navigate(`/cart`);
    }
  }, []);

  return (
    <>
      <div style={{ position: "fixed", top: 0, width: "100%", zIndex: 16 }}>
        <TitleHeader title={"運送方法"} back={"/cart"} />
      </div>

      <div style={{ padding: "155px 30px 30px 30px", height: "100%" }}>
        <Space direction="vertical">
          <Radio.Group
            value={shippingFee}
            onChange={(x) => {
              console.log(x.target.value);
              setShippingFee(x.target.value);
            }}
          >
            <Space direction="vertical" size="middle">
              {shippingMethods.map((x, index) => {
                return (
                  <Radio value={index} key={index}>
                    <Badge.Ribbon text={`$${x.price}`} color="#dba59f">
                      <Space
                        direction="vertical"
                        style={{ width: "calc(100vw - 100px)" }}
                      >
                        <Card
                          title={x.name}
                          block
                          style={{
                            borderColor: shippingFee == index ? "#dba59f" : "",
                          }}
                        >
                          <Typography.Text>滿{x.free}免運</Typography.Text>
                        </Card>
                      </Space>
                    </Badge.Ribbon>
                  </Radio>
                );
              })}
            </Space>
          </Radio.Group>
        </Space>
        <div
          style={{
            padding: "10px",
          }}
        >
          <Row>
            <Col offset={0} span={24}>
              <Divider orientation="right">
                <Typography.Text>共 {totalProduct} 件商品</Typography.Text>
              </Divider>

              <ProDescriptions column={1} bordered={true}>
                <ProDescriptions.Item label="商品金額" valueType="text">
                  ${totalAmount}
                </ProDescriptions.Item>
                <ProDescriptions.Item label="活動折抵" valueType="text">
                  $0
                </ProDescriptions.Item>
                <ProDescriptions.Item label="運費" valueType="text">
                  {shippingFee == null
                    ? "未選擇"
                    : `$${getShippingFee(
                        totalAmount,
                        shippingMethods[shippingFee]
                      )}`}
                </ProDescriptions.Item>
                <ProDescriptions.Item label="總計" valueType="text">
                  $
                  {shippingFee == null
                    ? totalAmount
                    : totalAmount +
                      getShippingFee(totalAmount, shippingMethods[shippingFee])}
                </ProDescriptions.Item>
              </ProDescriptions>
            </Col>
          </Row>
          <Button
            type="primary"
            size={"large"}
            block
            style={{ marginTop: 20 }}
            disabled={shippingFee == null}
            onClick={() => {
              localStorage.setItem(
                "order",
                JSON.stringify({
                  product: totalAmount,
                  shippingFee: getShippingFee(
                    totalAmount,
                    shippingMethods[shippingFee]
                  ),
                  total:
                    shippingFee == null
                      ? totalAmount
                      : totalAmount +
                        getShippingFee(
                          totalAmount,
                          shippingMethods[shippingFee]
                        ),
                })
              );
              navigate(`/cart/payment`);
            }}
          >
            送出訂單
          </Button>
        </div>
      </div>
    </>
  );
};

export default App;
